@import url("https://fonts.googleapis.com/css?family=Cormorant+Garamond:400,700|Great+Vibes&display=swap");
@import url(https://fonts.googleapis.com/css?family=Akronim);
.sub-form {
  padding-top: 20px;
  font-family: 'Cormorant Garamond', serif;
  font-weight: 700;
  font-style: italic;
  position: relative;
  z-index: 3;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  margin-bottom: 20px; }
  .sub-form__input {
    border: none;
    font-family: 'Cormorant Garamond', serif;
    font-weight: 700;
    font-style: italic;
    margin: 0 2em;
    width: 200px;
    padding: 10px;
    font-size: 18px;
    color: #fff;
    height: 30px;
    border-bottom: 2px solid transparent;
    outline: none;
    background-color: transparent; }
    .sub-form__input:focus {
      border-bottom: 2px solid #c0b9b9; }
    .sub-form__input::placeholder {
      color: rgba(143, 140, 140, 0.582); }
  .sub-form__filter-wrapper {
    position: relative; }
  .sub-form__clear-button-wrapper {
    position: absolute;
    top: 50%;
    color: #fff;
    transition: all .4s;
    transform: translateY(-50%); }
  .sub-form__clear-icon {
    color: #fff; }

.subs-length-wrap {
  position: relative;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  z-index: 3;
  margin-bottom: 1em; }
  .subs-length-wrap .subs-length-text {
    font-size: 25px;
    text-decoration: underline;
    font-family: 'Great Vibes', cursive;
    color: #fff; }

.subs-list {
  display: flex;
  font-family: 'Cormorant Garamond', serif;
  font-weight: 400;
  height: calc(100vh - 250px);
  overflow-y: auto;
  padding: 1em 2em;
  margin-bottom: 3em;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  position: relative;
  transition: all 0.5s;
  z-index: 3; }
  .subs-list__item {
    max-width: 25%;
    min-width: 326px;
    border-radius: 20px;
    color: #d1d0d0;
    transform: rotateX(50deg);
    padding: 1em;
    position: relative;
    transition: all 0.5s;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .subs-list__item:hover {
      border-color: white;
      transform: translateX(-10px) scale(1.1) rotateX(10deg);
      box-shadow: 6px 6px 11px -2px #6b6b6b; }
    .subs-list__item:hover .image-wrapper {
      animation-name: heartBeat;
      animation-duration: 1.3s;
      animation-timing-function: ease-in-out;
      animation-iteration-count: infinite; }
    .subs-list__item .image-wrapper {
      width: 40px;
      height: 40px;
      background-color: transparent;
      border: none;
      outline: none; }
      .subs-list__item .image-wrapper .item-image {
        display: block;
        width: 100%;
        height: auto;
        background-repeat: no-repeat;
        background-position: center; }
  .subs-list__item--loader {
    display: flex;
    padding-top: 10px;
    justify-content: center;
    align-items: center; }

@keyframes heartBeat {
  0% {
    transform: scale(1); }
  14% {
    transform: scale(1.3); }
  28% {
    transform: scale(1); }
  42% {
    transform: scale(1.3); }
  70% {
    transform: scale(1); } }

.auth-switch {
  text-decoration: none;
  color: #2121bb; }
  .auth-switch:hover {
    text-decoration: underline; }
  .auth-switch:visited {
    color: #2121bb; }

.mouse-trail {
  position: fixed;
  top: 0;
  left: 0; }

.loader-16 {
  transform: rotateZ(45deg);
  z-index: 9999999;
  perspective: 1000px;
  border-radius: 50%; }
  .loader-16:before, .loader-16:after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: inherit;
    height: inherit;
    border-radius: 50%;
    animation: 1s spin linear infinite; }
  .loader-16:before {
    transform: rotateX(70deg); }
  .loader-16:after {
    transform: rotateY(70deg);
    animation-delay: 0.4s; }

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%) rotateZ(0deg); }
  100% {
    transform: translate(-50%, -50%) rotateZ(360deg); } }

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%) rotate(0deg); }
  100% {
    transform: translate(-50%, -50%) rotate(-360deg); } }

@keyframes spin {
  0%,
  100% {
    box-shadow: 0.2em 0px 0 0px currentcolor; }
  12% {
    box-shadow: 0.2em 0.2em 0 0 currentcolor; }
  25% {
    box-shadow: 0 0.2em 0 0px currentcolor; }
  37% {
    box-shadow: -0.2em 0.2em 0 0 currentcolor; }
  50% {
    box-shadow: -0.2em 0 0 0 currentcolor; }
  62% {
    box-shadow: -0.2em -0.2em 0 0 currentcolor; }
  75% {
    box-shadow: 0px -0.2em 0 0 currentcolor; }
  87% {
    box-shadow: 0.2em -0.2em 0 0 currentcolor; } }

.jelly-button {
  display: inline-block;
  position: relative;
  min-height: 70px;
  padding: 10px 15px;
  font-family: 'Cormorant Garamond', serif;
  font-weight: 400;
  font-style: italic;
  width: 200px;
  top: 0;
  font-size: 20px;
  font-family: 'Open Sans', Helvetica;
  border-radius: 20px;
  background-color: transparent;
  border-bottom: 2px solid white;
  box-shadow: 0px 0px 0px rgba(15, 165, 60, 0.1);
  transform: translateZ(0);
  transition: all 0.1s ease;
  outline: none; }
  .jelly-button:hover {
    box-shadow: 0px 20px 20px rgba(13, 14, 13, 0.466);
    border-color: #bcbe10; }
  .jelly-button:active {
    transform: translateY(0) rotateX(0);
    box-shadow: 0px 0px 0px rgba(15, 165, 60, 0); }

.animated-gradient {
  width: 100%;
  min-height: 100vh;
  color: #fff;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: Gradient 15s ease infinite; }

@keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

.super-slide {
  background: linear-gradient(-45deg, #4bc0c8 25%, #feac5e 25%, #feac5e 50%, #4bc0c8 50%, #4bc0c8 75%, #feac5e 75%, #feac5e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 20px 20px;
  background-position: 0 0;
  animation: stripes 1s linear infinite; }

@keyframes stripes {
  100% {
    background-position: 20px 0, 20px 0, 20px 0; } }

.fire {
  background-position: center center;
  background-size: 1000px 200px;
  border-radius: 10px;
  font-family: 'Akronim';
  overflow: hidden;
  text-align: center;
  vertical-align: middle; }

.fire:before {
  content: '';
  vertical-align: middle; }

.Blazing {
  display: inline-block;
  margin: 0;
  color: #ff7300;
  outline: none;
  vertical-align: middle;
  text-shadow: 0 3px 20px red, 0 0 20px red, 0 0 10px orange, 4px -5px 6px yellow, -4px -10px 10px yellow, 0 -10px 30px yellow;
  animation: 2s Blazing infinite alternate linear; }

@keyframes Blazing {
  0% {
    text-shadow: 0 3px 20px red, 0 0 20px red, 0 0 10px orange, 0 0 0 yellow, 0 0 5px yellow, -2px -5px 5px yellow, 4px -10px 10px yellow; }
  25% {
    text-shadow: 0 3px 20px red, 0 0 30px red, 0 0 20px orange, 0 0 5px yellow, -2px -5px 5px yellow, 3px -10px 10px yellow, -4px -15px 20px yellow; }
  50% {
    text-shadow: 0 3px 20px red, 0 0 20px red, 0 -5px 10px orange, -2px -5px 5px yellow, 3px -10px 10px yellow, -4px -15px 20px yellow, 2px -20px 30px rgba(255, 255, 0, 0.5); }
  75% {
    text-shadow: 0 3px 20px red, 0 0 20px red, 0 -5px 10px orange, 3px -5px 5px yellow, -4px -10px 10px yellow, 2px -20px 30px rgba(255, 255, 0, 0.5), 0px -25px 40px rgba(255, 255, 0, 0); }
  100% {
    text-shadow: 0 3px 20px red, 0 0 20px red, 0 0 10px orange, 0 0 0 yellow, 0 0 5px yellow, -2px -5px 5px yellow, 4px -10px 10px yellow; } }

